.Intro {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.introSection1 {
  padding: 8rem 10rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
}

.introSection1 p {
  text-align: justify;
  font-weight: 500;
  color: #333333;
  letter-spacing: 1px;
  margin-bottom: 30px;
}

.introSection1 a {
  text-decoration: none;
  background-color: #784cfb;
  padding: 0.6rem 1.2rem;
  color: #ffffff;
  font-weight: 500;
  border-radius: 4px;
  transition: all 200ms;
}
.introSection1 a:hover {
  background-color: #784cfb;
  opacity: 0.9;
}
.introSection2 {
  display: flex;
  justify-content: center;
  align-items: center;
}

:root {
  --track-width: 6px;
  --primary-color: #784cfb;
}

* {
  margin: 0;
  padding: 0;
}

.animation-container {
  margin: 6rem 0;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.animation-container img {
  clip-path: ellipse(44.5% 50% at 50% 50%);
  height: 25rem;
  position: absolute;
  top: -11%;
  z-index: 1;
}

.inner,
.middle,
.outer {
  border-radius: 50%;
  border: var(--track-width) solid var(--primary-color);
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.outer {
  height: 20rem;
  aspect-ratio: 1/1;
  animation: revolution 30s linear infinite;
}

.middle {
  height: 15rem;
  aspect-ratio: 1/1;
  animation: revolution 20s linear infinite;
}

.inner {
  height: 10rem;
  aspect-ratio: 1/1;
  animation: revolution 10s linear infinite;
}

.inner::before,
.middle::before,
.outer::before,
.inner::after,
.middle::after,
.outer::after {
  content: "";
  height: 1.6rem;
  aspect-ratio: 1/1;
  background-color: #ffffff;
  position: absolute;
}
.orbits::before,
.orbits::after {
  content: "";
  aspect-ratio: 1/1;
  border-radius: 50%;
  border: var(--track-width) solid var(--primary-color);
  position: absolute;
}
.orbits::before {
  height: 4rem;
}
.orbits::after {
  height: 5rem;
}
.outer::before {
  left: -0.8rem;
}
.outer::after {
  top: -0.8rem;
}
.middle::before {
  bottom: -0.8rem;
}
.middle::after {
  right: -0.8rem;
}
.inner::before {
  top: -0.8rem;
}
.inner::after {
  right: -0.8rem;
}
.orbits::before {
  top: -15%;
}
.orbits::after {
  bottom: -20%;
}

.orbits {
  height: 25rem;
  aspect-ratio: 1/1;
  animation: revolution 40s linear infinite;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 600px) {
  .Intro {
    position: relative;
    overflow: hidden;
    display: grid;
    grid-template-columns: none;
  }
  .introSection2 {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-row: 1;
  }
  .introSection1 {
    position: relative;
    grid-row: 2;
    padding: 1rem;
    height: min-content;
    display: flex;
    flex-direction: column;
  }
  .introSection1 p {
    text-align: justify;
    font-size: 18px;
  }
  .introSection1 a {
    margin: 1rem 0;
    font-size: 18px;
    align-self: flex-end;
    background-color: var(--primary-color);
    color: #ffffff;
    text-decoration: none;
    padding: 0.6rem 2rem;
    border-radius: 4px;
  }
  .orbits {
    position: relative;
    height: 13rem;
    aspect-ratio: 1/1;
    border-radius: 50%;
    margin: 7rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: revolution 10s linear infinite;
  }
  .orbits::before,
  .orbits::after {
    position: absolute;
    content: "";
    aspect-ratio: 1/1;
    border-radius: 50%;
    border: 4px solid var(--primary-color);
    border-radius: 50%;
  }
  .orbits::before {
    height: 13%;
    right: -8%;
    top: -8%;
  }
  .orbits::after {
    height: 26%;
    bottom: -18%;
    left: -18%;
  }
  .outer,
  .middle,
  .inner {
    position: relative;
    border: 4px solid var(--primary-color);
    border-radius: 50%;
    aspect-ratio: 1/1;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: revolution 10s infinite linear;
  }
  .outer::before,
  .middle::before,
  .inner::before,
  .outer::after,
  .middle::after,
  .inner::after {
    content: "";
    width: 1rem;
    background-color: #ffffff;
    aspect-ratio: 1/1;
    position: absolute;
  }
  .outer::before,
  .middle::before,
  .inner::before {
    top: -0.5rem;
  }
  .outer::after,
  .middle::after,
  .inner::after {
    bottom: -0.5rem;
  }
  .outer {
    height: 100%;
  }
  .middle {
    height: 78%;
  }
  .inner {
    height: 69%;
  }

  .animation-container {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .animation-container img {
    top: unset;
    clip-path: ellipse(47.6% 50% at 50% 50%);
    height: 15rem;
    position: absolute;
    bottom: 26.7%;
    z-index: 1;
  }
}
@keyframes revolution {
  from {
    rotate: 360deg;
  }
}
